body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.calendar-day{min-height:100px;display:flex;flex-direction:column}.calendar-day__day{font-weight:bold;margin:5px}.calendar-day__day--other-month{color:#999}

.summarized-reminder{margin:0px 5px 5px;padding:3px;cursor:pointer}.summarized-reminder p{margin:0;color:#000}.summarized-reminder__name{font-weight:bold}

.calendar-table{width:100%;border-collapse:collapse;border:1px solid #4d4d4d}.calendar-table__header-cell{width:calc(100% / 7);color:#fff;background-color:#2f74b5;padding:5px;border:0}.calendar-table__body-table-data{border:1px solid #4d4d4d;background-color:#fff;width:calc(100% / 7);cursor:pointer}.calendar-table__body-table-data:hover{background-color:#f2f2f2}.calendar-table__body-table-data:first-child,.calendar-table__body-table-data:last-child{background-color:#e6e6e6;color:#2f74b5}.calendar-table__body-table-data:first-child:hover,.calendar-table__body-table-data:last-child:hover{background-color:#d9d9d9}

.simple-modal{display:block;position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.6)}.simple-modal__main{position:fixed;background:white;height:auto;top:50%;left:50%;transform:translate(-50%, -50%);width:320px}.simple-modal__actions{display:flex;justify-content:flex-end;padding:10px}

