ive,.open>.button--secondary.dropdown-toggle{color:#fff;background-color:gray;border-color:#6e6e6e}.button--secondary:active:hover,.button--secondary:active:focus,.button--secondary:active.focus,.button--secondary.active:hover,.button--secondary.active:focus,.button--secondary.active.focus,.open>.button--secondary.dropdown-toggle:hover,.open>.button--secondary.dropdown-toggle:focus,.open>.button--secondary.dropdown-toggle.focus{color:#fff;background-color:#6e6e6e;border-color:#4d4d4d}.button--secondary:active,.button--secondary.active,.open>.button--secondary.dropdown-toggle{background-image:none}.button--secondary.disabled,.button--secondary.disabled:hover,.button--secondary.disabled:focus,.button--secondary.disabled.focus,.button--secondary.disabled:active,.button--secondary.disabled.active,.button--secondary[disabled],.button--secondary[disabled]:hover,.button--secondary[disabled]:focus,.button--secondary[disabled].focus,.button--secondary[disabled]:active,.button--secondary[disabled].active,fieldset[disabled] .button--secondary,fieldset[disabled] .button--secondary:hover,fieldset[disabled] .button--secondary:focus,fieldset[disabled] .button--secondary.focus,fieldset[disabled] .button--secondary:active,fieldset[disabled] .button--secondary.active{background-color:#999;border-color:#8c8c8c}.button--secondary .badge{color:#999;background-color:#fff}

.color-picker__swatch{padding:5px;background:#fff;border-radius:1px;box-shadow:0 0 0 1px rgba(0,0,0,0.1);display:inline-block;cursor:pointer}.color-picker__color{width:36px;height:14px;border-radius:2px}

.reminder-modal{padding:10px}.reminder-modal__header{margin:10px 0;color:#2f74b5}.reminder-modal__group{margin:10px 0}.reminder-modal__label{font-weight:bold}.reminder-modal__label span{font-weight:normal}.reminder-modal__button-group{display:flex;flex-wrap:wrap;justify-content:center}.reminder-modal__button-group button{margin-bottom:10px}

.city-weather-view{border:1px solid #2f74b5;border-radius:10px;padding:10px}.city-weather-view__row{display:flex}.city-weather-view__icon-column{display:flex;flex-direction:column;justify-content:center;padding:5px}.city-weather-view__header{margin:5px 0}.city-weather-view__paragraph{margin:0}.city-weather-view__paragraph span{margin-left:5px}.city-weather-view__image{background:black;border-radius:50%;width:50px;height:50px}

.calendar-header{display:flex;justify-content:space-between;padding:5px 0;align-items:center}.calendar-header__head{display:flex;align-items:center;flex-direction:column;color:#2f74b5}.calendar-header__head h1{margin:0}.calendar-header__head span{margin-left:10px;cursor:pointer}.calendar-header__left-arrow,.calendar-header__right-arrow{cursor:pointer}.calendar-header__left-arrow polygon,.calendar-header__right-arrow polygon{fill:#2f74b5}.calendar-header__left-arrow{transform:rotateZ(-90deg)}.calendar-header__right-arrow{transform:rotateZ(90deg)}


